@import '../less/base.less';
@import '../re-button/button.less';
/*
Accrodion Style
*/
.reuseBundleElement___{
  margin-bottom: 20px;
}

.reuseBundleWrapper{
  > div {
    display: flex;
    flex-direction: row;
    width: 100%;
  }

  .reuseAccordionItem{
      // overflow: hidden;
      margin-bottom: 10px;
      position: relative;
      width: 100%;

      .reuseAccordionHeaderWrapper{
        display: flex;
        flex-direction: row;

        .reuseAccordionHeader{
            font-size: 14px;
            font-weight: 700;
            color: @_reuse--Color-Black-737373;
            display: flex;
            align-items: center;
            justify-content: space-between;
            overflow: hidden;
            background-color: #fff;
            width: 100%;
            height: 42px;
            border: 0;
            outline: 0;
            line-height: 40px;
            padding: 0;
            margin-left: 0;
            padding-right: 20px;
            padding-left: 20px;
            cursor: pointer;
            text-align: left;
            border: 1px solid @_reuse--Color-Border-Color;
            border-left: 0;
            border-right: 0;
            .reuse--Transition;

            span{
              padding: 0;
              width: 30px;
              height: 30px;
              background-color: @_reuse--Color-Gray-F3F3F3;
              display: -webkit-inline-flex;
              display: -ms-inline-flex;
              display: inline-flex;
              align-items: center;
              justify-content: center;
              .reuse--BorderRadius(50%);
            }
        }

        .reuseBundleDeleteButton{
            padding: 0;
            width: 42px;
            height: 42px;
            text-align: center;
            display: inline-block;
            border: 1px solid @_reuse--Color-Black-454545;
            background: none;
            background-color: @_reuse--Color-Black-454545;
            color: #ffffff;
            outline: 0;
            .reuse--BorderRadius(0);

            &.reuseButtonDisable{
              border: 1px solid @_reuse--Color-Border-Color;
              background-color: @_reuse--Color-Gray-F3F3F3;
              color: #a3a3a3;
              cursor: not-allowed;
            }

            i{
                font-size: @_reuse--FontSize;
                margin-right: 0;
            }
        }
      }

      .reuseAccordionBody{
          // display: flex;
          // flex-direction: column;
          // justify-content: flex-start;
          // align-items: flex-start;
          border-top: 0;
          margin-top: -1px;
          border: 1px solid @_reuse--Color-Border-Color;
          background-color: #fff;
          width: calc(~"100% + 42px");
          // min-height: 200px;
          margin-left: -42px;
          resize: vertical;
          // .reuse--Transition;

          // > div{
          //   width: 100%;
          // }

          > div{
            width: 100%;
            padding: 20px;
            border: 0;
            margin: 0;
            overflow-y: auto;

            // > div{
            //     margin-bottom: 35px;
            //
            //     &:last-child{
            //         margin-bottom: 0;
            //     }
            // }
          }

          .reuseBundleComponent{
            position: relative;
            margin-bottom: 30px;

            &:last-child{
              margin-bottom: 0;
            }

            > div{
              margin-bottom: 0;
            }
          }
      }

      input,
      textarea{
          background-color: #fff !important;
      }

      &.itemOpen{
        .reuseAccordionHeaderWrapper{
          border-bottom: 0;

          .reuseAccordionHeader{
              background-color: #fff;
              // color: #ffffff;
          }
        }
      }
  }
}



