@import './base.less';

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
li,
input,
textarea,
span,
div {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

button {
  cursor: pointer;
}

.reuseFormWrapper {
  * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    &:before,
    &:after {
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }
  }

  .reuseLabelsWrapper {
    display: block;
    margin-bottom: 15px;
  }
  .reuseLabel {
    font-size: @_reuse--FontSize + 1;
    // font-weight: @_reuse--FontWeight-Bold;
    color: @_reuse--Color-Black-737373;
    display: block;
    overflow: hidden;
    line-height: 30px;
    margin: 0;
  }
  .reuseShortcodeSubtitle {
    font-size: @_reuse--FontSize - 1;
    font-weight: @_reuse--FontWeight-Regular;
    color: @_reuse--Color-Black-737373Light;
    line-height: 1.5;
    display: block;
    overflow: hidden;
    // margin: -5px 0 0;
    margin: 0;
  }
}

.reuseElementBlock {
  display: block;
  margin-bottom: 30px;
  outline: 0;

  &:last-child {
    margin-bottom: 0;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.reuseSingleElement {
  display: block;
  margin-bottom: 30px;
  outline: 0;
  position: relative;

  &:last-child {
    margin-bottom: 0;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

