.reuseBorderLess {
  border: 0;
  outline: 0;
}
.reuseRequiredAstrix,
.reuseErrorMsg {
  color: @_reuse--Color-Red;
  margin-left: 3px;
}
.reuseArrowMove {
  width: 42px;
  height: 42px;
  display: inline-block;
  float: left;
  text-align: center;
  background-color: #ffffff;
  .reuse--BorderRadius(0);
  cursor: move;
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  border: 1px solid @_reuse--Color-Border-Color;
  outline: 0;
  position: relative;
  z-index: 1;

  &:active {
    cursor: grabbing;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
  }

  i {
    font-size: @_reuse--FontSize + 5;
    color: @_reuse--Color-Black-737373;
    line-height: 40px;
  }
}

.reuseErrorMessage {
  font-size: @_reuse--FontSize - 1;
  font-weight: @_reuse--FontWeight-Regular;
  color: @_reuse--Color-Red;
  line-height: 1.5;
  display: block;
  overflow: hidden;
  text-align: right;
  margin-top: 5px;
}

/* * * * * * * * * * *\
    Column Styling
\* * * * * * * * * * */
.reuseColumnParentWrapper {
  // width: 100%;
  margin: 0 -15px;
  display: flex;
  flex-wrap: wrap;

  .reuseElementBlock {
    margin-top: 30px;
    margin-bottom: 0;

    &:first-child {
      margin-top: 0;
    }
  }

  .reuseColumnFull {
    width: 100%;
    padding: 0 15px;
    margin-bottom: 30px;
  }

  .reuseColumnThreeFourth {
    width: 75%;
    padding: 0 15px;
    margin-bottom: 30px;
  }

  .reuseColumnTwoThird {
    width: 66.666%;
    padding: 0 15px;
    margin-bottom: 30px;
  }

  .reuseColumnHalf {
    width: 50%;
    padding: 0 15px;
    margin-bottom: 30px;
  }

  .reuseColumnOneThird {
    width: 33.333%;
    padding: 0 15px;
    margin-bottom: 30px;
  }

  .reuseColumnOneFourth {
    width: 25%;
    padding: 0 15px;
    margin-bottom: 30px;
  }
}

.reuseSingleSettingField {
  margin-bottom: 30px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

/************************************************
            LABEL POSITIONING
************************************************/
// LABEL POSITION: HORIZONTAL
.horizontal {
  display: flex;
  align-items: center;

  .reuseLabelsWrapper {
    width: auto;
    flex-shrink: 0;
    margin-bottom: 0;
    margin-right: 15px;
  }
}



